h2 {
    font-weight: 400;
}

h3 {
    font-weight: 400;
}

.zx-topHead {
    display: flex;
    border-bottom: 4px solid black;
    padding: 0 10px;
}

.zx-topHead h2 {
    margin-bottom: 0;
}

.zx-topHead h2 {
    margin-right: 50px;
}

.zx-topHead2 {
    display: flex;
    justify-content: space-between;
    padding: 0 20px 0 40px;
}

.zx-topHead2 .zx-topHead2heading {
    border: 4px solid black;
    padding: 0 30px;
}

.zx-topHead3 {
    display: flex;
}

.zx-head3-box2 {
    border: 4px solid black;
    padding: 0 10px;
    width: 100%;
    margin: 0 20px;
}

.zx-head3-mini-box {
    border: 4px solid black;
    margin: 10px 0;
    display: flex;
    align-items: center;
}

.zx-topHead3 .zx-head3-mini1 {
    background: red;
    width: 4px;
    height: 50px!important;
    border-right: 4px solid black;
}

.zx-topHead3 .zx-head3-mini2 {
    width: 10px;
    height: 10px;
    background: purple;
    border: 4px solid black;
    margin: 0 10px;
}

.head3-main {
    display: flex;
    justify-content: space-between;
    width: 60%;
    margin: 0 10px;
}

.zx-head3-mini-box p {
    color: gray;
    margin: 0;
    font-size: 14px;
}

.zx-topHead3 h3 {
    margin: 0;
}

.zx-topHead3 .zx-head3-mini6 {
    display: flex;
    justify-content: flex-end;
    width: 30%;
}

.zx-topHead3 .zx-head3-mini6 button {
    border: 2px solid black;
    padding: 5px 1px;
    margin: 0 2px;
    background: white;
}

.zx-topHead3 .zx-head3-mini6 button a {
    text-decoration: none;
    color: black;
}

.zx-topHead4 {
    width: 55%;
    text-align: center;
}

.zx-topHead4 p {
    margin: 5px 0;
    color: red;
}

.last-head {
    display: flex;
    justify-content: space-between;
}

.last-head-box2 {
    display: flex;
    align-items: center;
}

.last-head-box2 small {
    font-size: 18px;
    font-weight: 500;
    margin-right: 10px;
}

.last-head-box2 button {
    border: 2px solid black;
    padding: 5px 1px;
    margin: 0 2px;
    background: white;
}

.last-head-box2 a {
    text-decoration: none;
    color: black;
}

.last-head-box2 input {
    border: 4px solid black;
}